import Vue from 'vue'
import Router from 'vue-router'
import Publication from '../components/publication.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/publication',
      name: 'Publication',
      component: Publication
    }
  ],
  mode: 'history'
})



