<template>
    <div >
        <b-navbar toggleable type="dark" variant="dark">
            <b-navbar-brand class="nav-bar" style="margin: auto;">
                <h1 style="text-align: center;"> Comentarios </h1>
            </b-navbar-brand>
        </b-navbar>
        <div id="first">
            <table class="table table-hover">
                        <tr>
                        <th scope="col">Date</th>
                        <th scope="col">Name</th>
                        <th scope="col">Text</th>
                        </tr>
                    <tbody>

                        <tr v-for="(data,index) in data_comments" :key="index">
                            <td class="td1">{{ data.date }}</td>
                            <td class="td2">{{ data.name }}</td>
                            <td class="td2">{{ data.text }}</td>
                            <td>
                                <b-button pill class="ibutton" variant="outline-danger">Delete</b-button>
                            </td>
                            <td>
                                <b-button pill class="ibutton" variant="outline-success">Update</b-button>
                            </td>
                        </tr>

                    </tbody>
            </table>
        </div>
    </div>
</template>
<script>
import axios from 'axios'
export default {
  data () {
    return {
      data_comments: []
    }
  },

  methods: {
    getData () {
      const path = 'https://dashboard-intro.herokuapp.com/services_api_public/v1/publications'
      axios.get(path)
        .then((res) => {
          this.data_comments = res.data.data
          console.log(this.data_comments.data)
        })
        .catch((error) => {
          console.error(error)
        })
    }
  },
  created () {
    this.getData()
  }
}
</script>

<style>

#first{
margin-right: 25px;
margin-left: 25px;
}

#input-12{
 align-content: center;
 width:100%;
 padding-left:20px;
 font-weight:bold;

}
#button-2{
  align-content: center;
  margin-left:40%;
}

.nav-bar{
  padding-left: 50px;;
}
.link{
  padding-right:100px;
  font-size: 20px;

}
.td1{
  width:15%;
}
.td2{
  width:70%;

}
</style>



